import React from 'react';
import './SettingMenu.css';
import { Menu } from 'antd';
import { Link } from 'react-router-dom';


function MenuSetting(){

  const logoutFunc = () => {
    localStorage.removeItem('ustk');
    window.location = '/'
  }

  return(
    <Menu className="setting-menu-container">
        <Menu.Item className="setting-menu-items">
            <Link to="">Qun l trang</Link>
        </Menu.Item>
        <Menu.Item className="setting-menu-items">
            <Link to="">Qun l nhm</Link>
        </Menu.Item>
        <Menu.Item className="setting-menu-items">
            <Link to="">Nht k hot ng</Link>
        </Menu.Item>
        <Menu.Item className="setting-menu-items">
            <Link to="">Ty chn bng tin</Link>
        </Menu.Item>
        <Menu.Item className="setting-menu-items">
            <Link to="/setting">Ci t</Link>
        </Menu.Item>
        <Menu.Item className="setting-menu-items">
            <span onClick={logoutFunc}>ng xut</span>
        </Menu.Item>
    </Menu>
)
};

export default MenuSetting;
